/* Blog styles */
.blog-content {
  line-height: 1.9;
  color: #101820;
}

.blog-content * {
  color: inherit;
}

/* Wiksze odstpy midzy wszystkimi elementami */
.blog-content > * {
  margin-bottom: 1.5rem;
}

.blog-content > *:last-child {
  margin-bottom: 0;
}

.blog-content ul {
  list-style: none;
  padding-left: 0;
}

.blog-content ul li {
  margin-bottom: 0.75rem;
}

/* Usu domylne bullet points - ikony s dodawane przez HTML w blokach highlight */
.blog-content ul li::before {
  content: none;
}

/* Dla list w blokach highlight z ikonami - nie dodawaj dodatkowych elementw */
.blog-content ul li.flex::before {
  content: none;
}

/* Style dla list z ikonami (bdzie obsugiwane przez custom HTML) */
.blog-content .icon-list {
  list-style: none;
  padding-left: 0;
}

.blog-content .icon-list li {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  margin-bottom: 1rem;
}

.blog-content .icon-list li::before {
  content: none;
}

/* Lepsze odstpy dla nagwkw w artykule - rwne padding gra/d */
.blog-content h2 {
  margin-top: 4rem;
  margin-bottom: 2rem;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  line-height: 1.1;
  font-weight: 900;
}

.blog-content h3 {
  margin-top: 3rem;
  margin-bottom: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  line-height: 1.2;
  font-weight: 800;
}

/* Wicej miejsca na obrazy i ikony */
.blog-content figure {
  margin: 4rem 0;
}

.blog-content img {
  border-radius: 0.5rem;
}

/* Style dla list w blokach highlight z ikonami */
.blog-content ul li.flex {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
}

.blog-content ul li.flex svg {
  flex-shrink: 0;
  margin-top: 0.25rem;
}

/* Lepsze odstpy midzy paragrafami */
.blog-content p {
  margin-bottom: 1.5rem;
}

/* Border dla separatorw - grubsze */
.border-t-3 {
  border-top-width: 3px;
}

/* Zaznaczenie tekstu w tym prostokcie - ciemny granat zamiast tego */
