/* Custom scrollbar for a more premium feel */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #101820;
}

::-webkit-scrollbar-thumb {
  background: #fee715;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #ffd600;
}

/* Scrolling animation for companies list */
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

.animate-scroll {
  animation: scroll 30s linear infinite;
}

.animate-scroll:hover {
  animation-play-state: paused;
}

/* Auto-scroll animation for testimonials - CSS transform (desktop only) */
@keyframes testimonialsScroll {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-50%);
  }
}

@media (min-width: 768px) {
  .testimonials-scroll-desktop {
    animation: testimonialsScroll 30s linear infinite;
    will-change: transform;
  }
  
  .testimonials-scroll-desktop.paused {
    animation-play-state: paused;
  }
}

/* Mobile: no animation, manual scroll only */
@media (max-width: 767px) {
  .testimonials-scroll-desktop {
    animation: none;
  }
}

/* Hide scrollbar on mobile for testimonials */
.scrollbar-hide {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
.scrollbar-hide::-webkit-scrollbar {
  display: none;  /* Chrome, Safari and Opera */
}

/* Glass effect for testimonials */
.glass-effect {
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

